.profile {
  padding-top: 60px;

  &__info {
    display: flex;
  }

  &__info-wrap {

  }

  &__affiliation {
    margin-right: 40px;

    p {
      margin-bottom: 32px;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
    }

    &--personal-information {
      p {
        font-weight: 400;
      }
    }
  }

  &__input {
    position: relative;
    input {
      border: none;
      border-bottom: 1px solid #000000;
      outline: none;
      display: block;
      margin-bottom: 26px;
    }

    &:before {
      position: absolute;
      content: '';
      height: 1px;
      width: 0;
      background-color: $accent-color;
      left: 0;
      bottom: 0;
      transition: $transition;
    }

    &:hover, &:focus, &:active {
      &:before {
        width: 100%;
      }
    }
  }

  .control {
    display: inline-block;
    margin-right: 59px;

    * {
      color: $accent-color;
      font-size: 18px;
      line-height: 21px;
      position: relative;
      margin-top: 5px;
      display: block;
      cursor: pointer;
      font-weight: 400;
      text-transform: capitalize;

      &:after {
        position: absolute;
        content: '';
        height: 1px;
        width: 100%;
        max-width: 100%;
        left: 0;
        bottom: 0;
        background-color: $accent-color;
        transition: $transition;
      }

      &:hover {
        color: #000;

        &:after {
          width: 0;
          background-color: #000;
        }
      }
    }
  }
}

