body {
  font-family: $font-family-sans-serif;
  background-color: #000000;
}

a {
  transition: $transition;

  &:hover {
    text-decoration: none;
  }
}

ul {
  margin: 0;
  padding: 0;

  li {
    list-style-type: none;
  }
}

h2 {
  font-size: $h2Fz;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  color: $bc;
}

p {
  margin: 0;
}

//inner-page
.inner-page {
  background-color: #fff;
  padding-top: 13px;
  padding: 40px;
  height: 100vh;
  position: relative;

  &--main-page {
    background-color: #000000;
    height: auto;
  }
}

//inner-page end

//caption
.caption-wrap {
  border-bottom: 1px solid $accent-color;
  padding-bottom: 19px;
  display: flex;
  justify-content: center;

  .caption {
    font-weight: 700;
    font-size: $capFzLg;
    line-height: 37px;
    color: #000000;
    margin: 0;
  }

  &--press-left {
    justify-content: space-between;
  }
}

//caption end

//return-button
.return-button {
  position: absolute;
  top: 50px;
  left: 40px;

  a {
    color: $accent-color;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    position: relative;
    padding-left: 29px;

    &:before {
      position: absolute;
      content: '';
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      width: 10.5px;
      height: 21px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

//return-button end


//btn
.btn-wrap {
  display: inline-block;

  &--windowPopup {
    width: 100%;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;

    .btn {
      color: #ffffff !important;

      &:hover {
        color: $accent-color !important;
      }
    }
  }
}

.btn {
  background-color: $accent-color;
  color: $secondary-color;
  outline: none;
  white-space: nowrap;
  min-width: 174px;
  min-height: 45px;
  border-radius: 30px;
  font-weight: 400;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: inherit;
  border: 1px solid $accent-color;
  transition: $transition;
  padding: 0 25px;

  &:hover {
    background-color: transparent;
    color: $accent-color;
    box-shadow: 0 8px 16px 0 rgba(205, 143, 56, .24);
  }

  &--table-reservation {
    &:hover {
      box-shadow: none;
    }
  }


}


.return-btn {
  margin-right: 30px;
  color: #000000;
  font-size: 18px;
  line-height: 21px;
  position: relative;

  &:hover {
    color: $accent-color;

    &:after {
      width: 0;
      background-color: $accent-color;
    }
  }

  &:after {
    position: absolute;
    content: '';
    width: 100%;
    left: 0;
    bottom: 0;
    height: 1px;
    background-color: #000;
    transition: $transition;
  }
}

//btn end


.pagination {
  margin: 0;
  padding: 0;
  list-style-type: none;

  &__item {
    display: inline-block;
    color: #868686;
    margin-right: 15px;

    a {
      color: #868686;

      &:hover {
        color: $accent-color;
      }
    }

    &--active {
      a {
        color: $accent-color;
      }
    }
  }
}


//user
.affiliation-user {
  h2 {
    font-size: 26px;
    color: #868686;
    font-weight: 700;
    margin: 0;
  }
}

//user end


.agreement {
  display: flex;
  justify-content: center;
  position: relative;

  input {
    margin-right: 27px;
    opacity: 0;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: -1;

    &:checked + label:before {
      border: 1px solid $accent-color;
    }

    &:checked + label:after {
      opacity: 1;
      transform: translateY(-70%) rotate(-40deg);
    }
  }

  label {
    color: #868686;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    margin: 0;
    margin-right: 5px;
    position: relative;
    cursor: pointer;

    &:before {
      position: absolute;
      content: '';
      width: 25px;
      height: 25px;
      left: -44px;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid #E5E5E5;
    }

    &:after {
      position: absolute;
      content: '';
      width: 15px;
      height: 8px;
      border-bottom: 2px solid $accent-color;
      border-left: 2px solid $accent-color;
      left: -39px;
      top: 50%;
      transform: rotate(45deg) scale(0.2);
      opacity: 0;
      transition: $transition;

    }
  }

  a {
    color: #868686;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    text-decoration: underline;

    &:hover {
      color: $accent-color;
      text-decoration: none;
    }
  }

  &--reservation-hall {
    display: block;
    margin-bottom: 30px;

    input {
      position: absolute;
    }

    label {
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
      color: $bc;
      padding-left: 36px;

      span {
        color: #A5A5A5;
      }

      &:before {
        left: 0;
      }

      &:after {
        left: 5px;
      }
    }
  }

  &--windowPopup {
    label {
      color: #000000;
    }
  }
}


input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;

}

input[type="number"] {
  -moz-appearance: textfield;
  text-align: center;
}


.quantity-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 20px;

  input {
    opacity: 1;
    position: relative;
    max-width: 50px;
  }

  &--arrival {
    justify-content: center;

    p {
      margin: 0;
    }
  }

}

.quantity-arrow-minus,
.quantity-arrow-plus {
  position: relative;
  width: 10px;
  height: 10px;

  &:before, &:after {
    position: absolute;
    content: '';
    background-color: $accent-color;
  }

  cursor: pointer;
  display: inline-block;
}

.quantity-arrow-minus {
  margin-right: 5px;

  &:before {
    height: 1px;
    width: 10px;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
}

.quantity-arrow-plus {
  margin-left: 5px;

  &:before, &:after {
    height: 1px;
    width: 10px;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }

  &:after {
    transform: translateY(-50%) translateX(-50%) rotate(90deg);
  }
}

.quantity-num {
  color: $bc;
}

.pay {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 48px;

  &--reservation {
    margin-bottom: 60px;
  }
}



