.service {
  padding-top: 35px;

  &__selected {
    border-bottom: 1px solid #cd8f38;
    padding-bottom: 40px;
  }

  &__name {
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    color: #000;
    margin-bottom: 20px;
  }

  &__selected-info {
    .additionally {
      margin-top: 20px;

      p {
        margin: 0;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
      }
    }
  }

  &__selected-date {
    display: flex;
    align-items: center;
    max-width: 500px;
    width: 100%;
    margin-bottom: 30px;

    span {
      font-size: 18px;
      line-height: 22px;
      color: #000000;
      font-weight: 400;
    }
  }

  &__sub-caption {
    margin: 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 25px;
    color: #000000;

    &--small {
      margin-top: 20px;
      font-size: 18px;
      line-height: 22px;
      color: #000000;
      font-weight: 500;
    }
  }

  &__img {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    height: 100%;
    position: relative;
  }

  &__img-wrap {
    max-width: 480px;
    width: 100%;

    img {
      width: 100%;
      max-width: 100%;
    }
  }

  &__img-control {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 13px;

    p {
      cursor: pointer;
      color: $accent-color;
      margin-left: 38px;
      margin-bottom: 0;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &__total-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 13px;
    width: 100%;

    p {
      font-size: 32px;
      line-height: 37px;
      margin: 0;
      font-weight: 700;
    }

    &--reservation {
      border-bottom: 1px solid $accent-color;
      padding-bottom: 11px;
      padding-top: 60px;
    }
  }

  &__total {
    p {
      color: $accent-color;
    }

    .change-currency {
      list-style-type: none;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      margin-top: 1px;

      &__item {
        display: inline-block;

        a {
          font-weight: 500;
          font-size: 18px;
          line-height: 21px;
          color: #868686;

          &:hover {
            color: $accent-color;
          }
        }

        &--active {
          a {
            color: $accent-color;
          }
        }
      }
    }
  }
}

