.reservation__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 20px;
  margin-left: 65px;
  align-items: center;


  .localization-lang {
    &__select {
      margin: 0;
      position: relative;
      background-color: #000;

      i {
        margin-left: 0;
        position: absolute;
        right: 5px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 9px;
        cursor: pointer;
      }
    }
  }

  .language-select {
    text-align: center;
    cursor: pointer;
    display: none;
    margin: 0;
    position: absolute;
    right: 0;
    background-color: #000;
  }

  .language-select li {
    list-style: none;
    background: #000;
    width: 100%;
    text-align: right;
    padding-right: 19px;
    padding-bottom: 9px;
    margin-left: 0;
  }


  .language-select li:hover {
    cursor: pointer;
    background: #000;
  }

  .lang {
    text-align: center;
    background: #000;
    display: inline-block;
    margin: 0;
    cursor: pointer;
    padding-left: 34px;
    padding-right: 19px;
    padding-top: 9px;
    padding-bottom: 9px;

  }


  .toggle {
    display: block!important;
    position: absolute;
    left: 0;
  }
}