.popover {
  position: absolute;
  top: 10px;
  left: 0;
  z-index: 1010;
  display: none;
  max-width: 276px;
  padding: 1px;
  text-align: left;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 6px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
  white-space: normal;
  margin-top: 5px;
  padding-bottom: 20px;

  .popover-content {
    text-align: center;
    border-top: 1px solid rgba(0, 0, 0, .2);
    border-bottom: 1px solid rgba(0, 0, 0, .2);
  }

  .clockpicker-button {
    margin: 0 auto;
    margin-top: 20px;
  }

  .text-primary {
    color: $accent-color;
  }

  .clockpicker-tick {
    &:hover {
      background-color: #c0e5f7;
    }
  }
}


.ui-datepicker {
  text-align: center;
  position: absolute;
  z-index: 99999999999;
  background-color: #fff;
  border-radius: 10px;
  padding: 40px 48px;
  max-width: 517px;
  width: 100%;
  min-height: 753px;
  margin: auto;
  display: none;
  left: 50%!important;
  top: 50% !important;
  transform: translateY(-50%) translateX(-50%);
  .ui-datepicker-calendar {
    margin-top: 27px;

    thead {
      tr {
        th {
          padding-bottom: 17px;

          span {
            color: #a5a5a5;
            border: none;
            background: none;
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
          }
        }
      }
    }

    tboby {
      tr {
        td {
          height: 45px;
          width: 45px;
          text-align: center;
          vertical-align: middle;
        }
      }
    }
  }

  .ui-state-default {
    text-align: center;
    color: #5A5A5A;
    border: none;
    background: none;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    transition: $transition;
    width: 45px;
    height: 45px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 12px;

    &:hover {
      background-color: $accent-color;
      color: #ffffff;;
    }
  }

  .ui-state-active {
    background-color: $accent-color;
    color: #ffffff;;
  }

  .ui-datepicker-header {
    background-color: transparent;
    border: none;
    display: block;
    max-width: 230px;
    margin: auto;
    width: 100%;
    text-align: center;
  }

  .ui-datepicker-title {
    font-weight: 700;
    color: #000000;
    font-size: 22px;
    line-height: 25px;
    margin: 0;
    display: inline-block;
  }
}


.ui-datepicker-prev {
  background-image: url('../../assets/icons/ar-calendar-left.svg');
}

.ui-datepicker-next {
  background-image: url('../../assets/icons/ar-calendar-right.svg');
}

.ui-icon-circle-triangle-e {

}

.ui-corner-all {
  border: 1px solid transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.ui-button:focus,
.ui-button:hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-content .ui-datepicker-next-hover,
.ui-widget-header .ui-state-focus {
  background-image: url('../../assets/icons/ar-calendar-right.svg');
  top: 2px;
  right: 2px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.ui-button:focus,
.ui-button:hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-content .ui-datepicker-prev-hover,
.ui-widget-header .ui-state-focus {
  background-image: url('../../assets/icons/ar-calendar-left.svg');
  top: 2px;
  left: 2px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;

}

.timepicker {
  text-align: center;
}

.select-month {
  border: 1px solid #868686;
  height: 45px;
  border-radius: 5px;
  background: none;
  outline: none;
  padding: 0 30px 0 20px;
  margin-bottom: 20px;
  background-image: url('../../assets/icons/arrow-down.svg');
  background-repeat: no-repeat;
  background-size: 10px 5px;
  background-position: 90%;
  appearance: none;
}