.history {
  .table {
    margin-top: 17px;
    margin-bottom: 40px;
    border-bottom: 1px solid #868686;

    thead {
      th {
        border-top: none;
        border-bottom: 1px solid $accent-color;
        font-weight: 500;
        font-size: 18px;
        padding: 20px;

        &:last-child {
          text-align: right;
        }
      }
    }

    td {
      font-weight: 400;
      font-size: 18px;
      border-top: 1px solid #868686;
      padding: 20px;

      &:last-child {
        text-align: right;
      }
    }
  }
}