.reservation {
  &__reservation-date {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    input {
      font-size: 15.5px;
    }
  }

  &__calendar {
    position: relative;
    input {

      position: relative;
      font-weight: 700;
      color: $accent-color;
      font-size: 26px;
      line-height: 30px;
      background: none;
      border: none;
      outline: none;
      cursor: pointer;
      margin-right: 10px;
      display: inline-block;
    }
  }

  &__date-img {
    margin-right: 10px;
  }

  &__panel-control {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }

  .authorization {
    * {
      color: #ffffff;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px;
      margin-left: 30px;
      transition: $transition;

      &:hover {
        color: $accent-color;
      }
    }

    &__authorized {
      font-weight: 400 !important;
      color: #a5a5a5;
      cursor: pointer;
      position: relative;

      i {
        margin: 0;
        font-size: 9px;
      }


      .dropdown-menu {
        position: absolute;
        right: 19px;
        text-align: right;
        width: 100%;
        background-color: #000;
        margin: 0;
        padding-right: 20px;
        display: none;

        li {
          margin: 0;

          a {
            margin: 0;
            display: block;
            min-height: 51px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
          }
        }
      }
    }


  }
}