.window-popup {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 40px 79px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 458px;
  display: none;
  position: absolute;
  top: 40px;
  z-index: 2;

  &__close {
    background-size: contain;
    width: 26px;
    height: 26px;
    background-repeat: no-repeat;
    right: -66px;
    top: 40px;
    cursor: pointer;
    transition: $transition;
    position: absolute;

    &:hover {
      transform: rotate(-90deg);
    }
  }


  h5 {
    font-size: 14px;
    text-align: left;
    display: block;
    width: 100%;
    font-weight: 400;
    margin-top: 40px;
    margin-bottom: 0;
  }

  p {
    //font-size: 16px;
    //line-height: 18px;
    //color: #868686;
    //margin-top: 22px;
    margin-bottom: 0;
    //text-align: center;
  }

  &__input {
    width: 100%;
    max-width: 100%;
    position: relative;
    margin-top: 35px;

    input {
      width: 100%;
      max-width: 100%;
    }
  }

  &__password-recovery {
    color: #A5A5A5;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    display: block;
    text-align: right;
    width: 100%;
    margin-top: 10px;
    transition: $transition;

    &:hover {
      color: $accent-color;
    }
  }


  form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    max-width: 100%;

    .label {
      transition: $transition;
      margin: 0;
      color: #868686;
      font-size: 18px;
      line-height: 22px;
      position: absolute;
      top: 0;

      &.filled {
        font-size: 12px;
        line-height: 14px;
        transform: translateY(-10px);
      }

      &.focused {
        color: #000;
      }
    }

    input {
      border: none;
      border-bottom: 1px solid #A5A5A5;
      outline: none;
      color: #868686;
      background-color: transparent;

      &:focus {
        border-color: #000;
        color: #000;
      }
    }


  }

  .caption {
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;
    color: #000000;
  }

  .btn {
    margin-top: 35px;
  }

  &--reservation {
    max-width: 1000px;
    padding: 60px;
  }

  &__caption {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    max-width: 100%;
    padding-bottom: 20px;
    border-bottom: 1px solid $accent-color;
    position: relative;
  }

  &__conference-hall {
    width: 100%;
    margin-bottom: 47px;
    margin-top: 40px;
  }

  &__reservation-hours {
    position: relative;

    input {
      max-width: 60px !important;
      width: 100%;
      border-bottom: 1px solid #A5A5A5;
      color: #a5a5a5;
      position: relative;
      text-align: center;
    }
  }

  &__reservation-date-wrap {

    width: 100%;
    margin: 40px 0 50px;

    p {
      color: $bc;
      display: inline-block;
      margin-right: 10px;
      position: relative;
      cursor: pointer;
    }
  }

  &__reservation-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
    margin-bottom: 20px;
  }

  .date-format {
    color: #000000;
    margin-right: 18px;

    &:active, &:focus {
      color: #000000;
    }
  }

  .checkWrap {

  }

  &__reservation-date {
    line-height: 0;
    p {
      margin-top: 0;

      img {
        width: 20px;
        position: absolute;
        left: -30px;
        bottom: 0;
      }
    }
  }

  &__date-img {
    max-width: 20px;
    margin-right: 10px;
    display: inline-block;
    img, svg {
      width: 100%;
      max-width: 100%;
    }
  }

  &__reservation-date-choose {
    margin-left: 30px;
    position: relative;
    font-size: 16px;
    line-height: 18px;
    color: #868686;
    margin-top: 22px;
    margin-bottom: 0;
    //text-align: center;
    &:hover {
      color: $accent-color;

      &:after {
        width: 0;
        background-color: $accent-color;
      }
    }

    &:after {
      position: absolute;
      content: '';
      height: 1px;
      width: 100%;
      background-color: $bc;
      left: 0;
      bottom: 0;
      transition: $transition;
    }
  }

  &__reservation-hours {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: flex-end;

    p {
      margin: 0 10px;
    }

    input {
      height: 20px !important;
    }


  }

  &__contact-details {
    width: 100%;
    justify-content: space-between;
    margin-bottom: 50px;

    .window-popup__input {
      max-width: 400px;
      display: inline-block;
      margin-right: 75px;

      &:nth-child(2n) {
        margin-right: 0;
      }
    }
  }

  &__additionally {
    display: none;
    width: 100%;
  }

  &__additionally-list {
    margin-top: 40px;
  }

  &__additionally-item {
    margin-bottom: 40px;
  }

  &__additionally-input {
    opacity: 1 !important;
    z-index: 1 !important;
    border: none !important;
    position: relative !important;
  }

  &__additionally-product {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: $bc;

    .agreement {
      margin: 0;
    }
  }

  &__additionally-product-total {
    margin-left: 65px;
  }

  &__more {
    &:hover {
      .window-popup__more-img {
        &:before {
          opacity: 1;
        }
      }
    }

    p {
      font-weight: 700;
      font-size: 22px;
      line-height: 25px;
      color: $bc;
      margin-top: 14px;
      margin-bottom: 0;
    }


  }

  &__more-img {
    max-width: 420px;
    width: 100%;
    margin-top: 40px;
    display: block;
    position: relative;

    &:before {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      opacity: 0;
      transition: $transition;
    }

    img {
      width: 100%;
    }
  }
}

.authorization-window {
  min-height: 376px;
}

.trigger-js {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: $accent-color;
  font-size: 20px;
  display: block;
  transition: $transition;
}

.rotate-arrow {
  transform: rotate(180deg) translateY(50%);

}


